@import "mixins";

:root {
  // layout
  --block-margin-left: 64px;
  --block-margin-right: 64px;
  --block-margin-top: 20px;
  --block-margin-bottom: 20px;
  --block-padding-left: var(--block-margin-left);
  --block-padding-top: var(--block-margin-top);
  --block-padding-bottom: var(--block-margin-bottom);
  --block-padding-right: var(--block-margin-right);
  --paragraph-margins: 16px 0 0 0;
  --header-height: 80px;
  --header-paddings: 24px 68px 16px 68px;
  --max-width: 1440px;
  --floor-margin: 256px;

  // font size
  --base-font: Montserrat;
  --h1-font-size: 40px;
  --h1-font: bold var(--h1-font-size) Montserrat;
  --h1-line-height: 48px;

  --h2-font: normal 32px Montserrat;
  --h2-line-height: 42px;

  --paragraph-font-size: 24px;
  --paragraph-font: normal var(--paragraph-font-size) Open Sans;
  --paragraph-line-height: 32px;

  --navigation-font: normal 16px Open Sans;

  // color
  --main-font-color: black;
  --main-white-color: white;

  --grid-color: rgb(248, 93, 31);
  --grid-color-light: rgb(268, 110, 54);
  --grid-color-opacity: rgba(248, 93, 31, 0.3);
  --grid-border: 14px solid var(--grid-color-opacity);

  --dr-color: rgb(26, 165, 184);
  --dr-color-light: rgb(46, 182, 207);
  --dr-color-opacity: rgba(26, 165, 184, 0.3);
  --dr-border: 14px solid var(--dr-color-opacity);

  --registry-color: rgb(138, 182, 90);
  --registry-color-light: rgb(158, 199, 113);
  --registry-color-opacity: rgba(138, 182, 90, 0.3);
  --registry-border: 14px solid var(--registry-color-opacity);

  --control-color: rgb(244, 197, 46);
  --control-color-light: rgb(264, 214, 63);
  --control-color-opacity: rgba(244, 197, 46, 0.3);
  --control-border: 14px solid var(--control-color-opacity);

  --header-background-color: transparent;

  @include desktop-s-and-less {
    --h2-font: normal 3vw Montserrat;
    --h1-font: bold 3vw Montserrat;
    --paragraph-font-size: 2vw;
    --paragraph-font: normal var(--paragraph-font-size) Open Sans;
    --paragraph-line-height: 2.6vw;
    --h2-line-height: 3.8vw;
    --h1-line-height: 3vw;
    --h1-font-size: 2.2vw;
  }

  @include m-tablet {
    // fonts
    --h2-font: normal 24px Montserrat;
    --h2-line-height: 30px;

    --h1-font-size: 24px;
    --h1-font: bold var(--h1-font-size) Montserrat;
    --h1-line-height: 30px;

    --paragraph-font-size: 20px;
    --paragraph-font: normal var(--paragraph-font-size) Open Sans;
    --paragraph-line-height: 24px;

    //layout
    --block-margin-left: 16px;
    --block-margin-right: 16px;
    --block-margin-top: 16px;
    --block-margin-bottom: 16px;
    --floor-margin: 128px;
    --header-paddings: 0 16px 16px 16px;

    // colors
    --grid-border: 7px solid var(--grid-color-opacity);
    --dr-border: 7px solid var(--dr-color-opacity);
    --registry-border: 7px solid var(--registry-color-opacity);
    --control-border: 7px solid var(--control-color-opacity);
  }

  @include s-tablet {
    --paragraph-font-size: 16px;
    --paragraph-font: normal var(--paragraph-font-size) Open Sans;
    --paragraph-line-height: 20px;
  }
}
